@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;800;900&display=swap);
:root {
  --font-nav: 'Roboto', sans-serif;
  --font-txt: 'Poppins', sans-serif;
  --font-title: 'Rockwell Condensed';

  --rojo: #E91E1E;
  --negro: #000000;
  --blanco: #fff;
  --dark-blanco: #eee;
  --light-color: #616563;
  --light-color-2: #EDEEEC;

  --footer-bg: #3D3E40;
  --eventos-bg: #D0D0D0;

  --box-shadow: 0 0.5rem 1.5rem rgba(0,0,0,.1);
  --border: .2rem solid rgba(0,0,0,.1);
  --outline: .1rem solid rgba(0,0,0,.1);
  --outline-hover: .2rem solid var(--rojo);
}

* {
  border: none;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  font-family: var(--font-txt);
  margin: 0;
  padding: 0;
  outline: none;
  text-decoration: none;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scroll-padding-top: 7rem;
}

body {
  background-color: #fff;
  background-color: var(--blanco);
  width: 100%;
}

.btn {
  margin-top: 1rem;
  display: inline-block;
  padding: .8rem 3rem;
  font-size: 1.7rem;
  border-radius: .5rem;
  border: 0.2rem solid #000000;
  border: 0.2rem solid var(--negro);
  color: #000000;
  color: var(--negro);
  cursor: pointer;
  background: none;
  text-align: center;
  margin: 10px auto 1px auto;
  transition: all .2s linear;
}

.btn:hover {
  background: #E91E1E;
  background: var(--rojo);
  color: #fff;
  color: var(--blanco);
}

.btn:disabled {
  border-color: lightgrey;
  color: lightgrey;
}

.btn:disabled:hover {
  background: none;
}

h1 {
  font-family: 'Rockwell Condensed';
  font-family: var(--font-title);
  color: #000000;
  color: var(--negro);
  font-size: 8rem;
  text-align: center;
  margin-top: 10rem;
}

h1 span {
  color: #E91E1E;
  color: var(--rojo);
  font-family: 'Rockwell Condensed';
  font-family: var(--font-title);
}


.contenedor{
  display: flex;
  flex-flow: row wrap;
  text-align: center;
  justify-content: space-evenly;
  margin: 10rem auto;

}

.caja{
  padding: 10px 40px;
  color: 'Poppins', sans-serif;
  color: var(--font-txt)
}


@media (max-width:991px) {
    
  html {
    font-size: 55%;
  }

}

@media (max-width:450px) {
    
  html {
      font-size: 50%;
  }

}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}
.meal-details {
    background: var(--rojo);
    border-radius: 1rem;
    color: var(--blanco);
    margin: 0 auto;
    margin-top: 10rem;
    width: 35%;
    padding: 2rem 0;
    text-align: center;
    position: relative;
}

.meal-content {
    margin: 2rem;
}

.close {
    position: absolute;
    right: 2rem;
    top: 2rem;
    font-size: 2rem;
    background: var(--blanco);
    border: none;
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
    cursor: pointer;

    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0.8;
}

.close:hover {
    opacity: 1;
}

.recipe-title {
    letter-spacing: 1px;
    padding-bottom: 1rem;
    font-size: 4rem;
    font-family: var(--font-title);
}

.recipe-ingredient {
    background: var(--blanco);
    font-weight: 600;
    color: var(--rojo);
    display: inline-block;
    padding: 0.5rem 0.8rem;
    border-radius: 0.3rem;
    font-size: 1.5rem;
}

.recipe-instructions {
    padding: 1rem 0;
}

.recipe-img {
    margin: 0 auto;
    width: 80%;
}

.recipe-img img {
    border-radius: 1rem;
    width: 80%;
}

@media screen and (max-width: 1100px) {
    .meal-details {
        width: 90%;
    }
}
.menu {
    margin: 0 auto 100px auto;
    text-align: center;
    width: 80%;
    position: relative;
}

.menuItem-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
    grid-gap: 1.5rem;
    gap: 1.5rem;
    margin-top: 30px;
}

.menuItem {
    padding: 3rem 2rem;
    background: var(--blanco);
    outline: var(--outline);
    outline-offset: -1rem;
    text-align: center;
    box-shadow: var(--box-shadow);
    transition: .2s linear;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.menuItem:hover {
    outline: var(--outline-hover);
    outline-offset: 0rem;
}

.menuItem img {
    margin: 1rem 0;
    width: 25rem;
    height: 18rem;
}

.menuItem-container h3 {
    font-family: var(--font-title);
    font-size: 2rem;
}

.menuItem-container p {
    font-family: var(--font-title);
    font-size: 2rem;
}

.loading {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
